<template>
  <el-dialog
    class="m-orderManage-auditDialog"
    :visible="visible"
    title="财务审核"
    append-to-body
    width="900px"
    @close="closeEvent">
    <el-row v-if="target.finance.status === -1" style="padding: 10px 20px;border:2px red dotted;margin-bottom:20px;">
      驳回理由：{{ target.bill_info.reject }}
    </el-row>
    <div class="detail-title" style="border-bottom:none;">客户：<el-button style="font-size:16px;" @click.native.prevent="onCheckEvent" type="text">{{ target.customer.name }}</el-button></div>
    <el-form :model="item" :rules="rules" ref="itemForm" label-position="right" label-width="150px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="收款人(借款人)" prop="customer_name">
            <el-col :span="14">
              <el-input v-model="item.customer_name" controls-position="right"></el-input>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="银行名" prop="bank_name">
            <el-col :span="14">
              <el-input v-model="item.bank_name" controls-position="right"></el-input>
            </el-col>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="收款人银行卡账户" prop="bank_number">
            <el-col :span="14">
              <el-input v-model="item.bank_number" controls-position="right"></el-input>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="币别" prop="money_type">
            <el-col :span="14">
              <el-input v-model="item.money_type" controls-position="right"></el-input>
            </el-col>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="收款(借款)金额" prop="money">
            <el-col :span="14">
              <el-input-number v-model="item.money" controls-position="right"></el-input-number>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="日期" required>
            <el-col :span="14">
              <el-form-item prop="date">
                <el-date-picker type="date" placeholder="开始日期" value-format="yyyy-MM-dd" v-model="item.date" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="支付方式" prop="lean_type">
            <el-col :span="14">
              <el-input v-model="item.lean_type" controls-position="right"></el-input>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <div style="border:1px #ddd solid;margin:20px;padding: 20px;">
        <div v-for="attach in item.attachment" :key="attach.id" style="border: 1px #ddd dotted;padding: 20px 20px 0 20px;margin-bottom:10px;">
          <el-row>
            <el-col :span="12">
              <el-form-item label="业务员">
                <el-col :span="18">
                  <el-input v-model="attach.fee_descript" controls-position="right"></el-input>
                </el-col>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item :label="attach.id == 1?'手续费（元）':'返点金额（元）'">
                <el-col :span="14">
                  <el-input-number v-model="attach.fee" controls-position="right"></el-input-number>
                </el-col>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="银行信息">
                <el-col :span="22">
                  <el-input v-model="attach.bank_info" controls-position="right"></el-input>
                </el-col>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </div>
      <el-row>
        <el-col :span="12">
          <el-form-item label="备注">
            <el-input :style="{width: '300px'}" v-model="item.opinion" type="textarea" placeholder="请输入描述" :rows="4" />
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <!-- <el-button v-if="item.status === 1" type="primary" @click="onDownload">文件下载</el-button> -->
      <el-button type="primary" @click="onPassEvent">通 过</el-button>
      <!-- <el-button type="warning" @click="showEditDialog = false">驳 回</el-button> -->
      <el-button @click="visible = false">取 消</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  props:['target'],
  data(){
    return{
      visible: true,
      item: {
        score_card:{}
      },
      rules: {
        customer_name:[
          {type: 'string', required: true, message: '请填写收款人(借款人)姓名'}
        ],
        bank_number:[
          {type: 'string', required: true, message: '请填写收款人银行卡账户'}
        ],
        bank_name:[
          {type: 'string', required: true, message: '请填写收款人银行卡名'}
        ],
        money_type:[
          {type: 'string', required: true, message: '请填写币别'}
        ],
        lean_type:[
          {type: 'string', required: true, message: '请填写支付方式'}
        ],
        money:[
          { type: 'number', required: true, message: '请填写收款(借款)金额', trigger: 'blur' }
        ],
        date:[
          { type:'string', required: true, message: '请填写日期', trigger: 'change' }
        ],
      },

      products:[
        {name: '产品1', id: '1'},
        {name: '产品2', id: '2'}
      ],
      types:[
        {name: '类型1', id: '1'},
        {name: '类型2', id: '2'}
      ]
    }
  },
  computed:{
    info(){
      return this.item.wind_control;
    }
  },
  methods:{
    onCheckEvent(){
      this.$emit('detail');
    },
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    handleUploadSuccess(response, file){
      
    },
    onPassEvent(){
      this.$refs['itemForm'].validate((valid) => {
        if(valid){
          this.$emit('pass', this.item);
        }else{
          this.$message('请正确填写表单！');
        }
      });
    },
    onDownload(){
      let uid = sessionStorage.getItem('ts_user_id');
      window.open(`/v1/inspect/${uid}/finance/file?finance_id=${this.item.finance_id}`);
    }
  },
  mounted(){
    if(!this.target.finance.finance_id){
      this.item = {   
        "bill_id": this.target.bill_info.bill_id,
        "bill_status":5,  //审核之后的订单状态 (驳回到之前的任意一个审核状态，或者下个状态)
        "status": 1,        //财务审核状态
        "opinion":"无",      //审核意见          
        "customer_name": this.target.customer.name, //收款人(借款人)姓名
        "bank_number": this.target.customer.bank_card_num, //收款人银行卡账户
        "bank_name": this.target.customer.bank,    //银行名
        "money": 0,    //收款(借款)金额小写 (大写)
        "date": "2018-01-01", //日期
        "money_type":"人民币",    //币别
        "lean_type":"银行转账", //支付方式
        "attachment":[         //备注
          {	
            "id": 1,
            "fee_descript":"",
            "fee": 0,
            "bank_info":""
          },
          {	
            "id": 2,
            "fee_descript":"",
            "fee": 0,
            "bank_info":""
          }
        ]
      }
    }else{
      this.item = this.target.finance;
      this.item.bill_id = this.target.bill_info.bill_id;
      this.item.bill_status = 5;
    }
  }
}
</script>
<style scoped>
  .m-orderManage-auditDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
    padding: 0 30px;
  }
  .m-orderManage-auditDialog .el-form {
    padding: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel{
    border: 2px #ddd dotted;
    margin-bottom: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel .title{
    padding: 20px;
    font-size: 16px;
    font-weight: bold;
  }
</style>




